body{padding:0;margin:0}img{width:100%;height:100%;background-color:white}.gameOver{animation:blinker 1.5s linear infinite;color:red}@keyframes blinker{50%{opacity:0}}.hidden{opacity:0}.remove{display:none}.apply-shake{animation:shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both}@keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, 0, 0)}}.main-container{display:block;margin:auto}.main-container .menu-container{display:flex;justify-content:center}.main-container .menu-container .start-container{display:flex;flex-direction:column;align-items:center;justify-content:center}.main-container .menu-container .start-container .start{background-color:red;border-radius:8px;border:0;cursor:pointer;color:#ffffff;font-size:18px;padding:8px 25px;margin:10px}.main-container .menu-container .start-container .start:hover{background-color:#29cf1a}.main-container .menu-container .inform-pannel{display:block;justify-content:center}.main-container .menu-container .inform-pannel .options{display:flex;justify-content:center;font-family:-apple-system, Ubuntu}.main-container .menu-container .inform-pannel .all-controllers{display:flex}.main-container .menu-container .inform-pannel .all-controllers .controller{display:flex;align-items:center;flex-direction:column;margin:0 15px 0 15px}.main-container .menu-container .inform-pannel .all-controllers .controller .label{display:none}.main-container .menu-container .inform-pannel .all-controllers .controller .arrow{width:0;height:0;border:0 solid transparent;cursor:pointer}.main-container .menu-container .inform-pannel .all-controllers .controller .up{border-right-width:14px;border-left-width:13px;border-bottom:18px solid teal}.main-container .menu-container .inform-pannel .all-controllers .controller .up:hover{border-bottom:18px solid red}.main-container .menu-container .inform-pannel .all-controllers .controller .up-limit{border-right-width:14px;border-left-width:13px;border-bottom:18px solid grey;cursor:auto}.main-container .menu-container .inform-pannel .all-controllers .controller .down{border-left-width:14px;border-right-width:13px;border-top:18px solid teal}.main-container .menu-container .inform-pannel .all-controllers .controller .down:hover{border-top:18px solid red}.main-container .menu-container .inform-pannel .all-controllers .controller .down-limit{border-left-width:14px;border-right-width:13px;border-top:18px solid grey;cursor:auto}.main-container .menu-container .timer{margin:10px;display:block;text-align:center}.main-container .field-container{display:block;margin:auto;width:max-content;background-image:url(img/bg6.ce758.png)}.main-container .field-container .row{display:flex;justify-content:center}.main-container .field-container .cells{width:15vh;height:15vh;margin:1px;border:0;padding:0;background-image:url(img/question.d4d88.jpg);background-size:cover}.main-container .field-container .cells:hover{filter:contrast(110%);background-color:white}@media all and (max-width: 599px){.main-container .menu-container .start-container .start{padding:8px 8px}.main-container .menu-container .inform-pannel .all-controllers .controller{margin:0 10px 0 10px}.main-container .field-container .cells{width:15vw;height:15vw;margin:1px}}
